import React from 'react';
import classes from './Users.module.css';
import userPhoto from '../../assets/images/no_avatar.png';

let Users = (props) => {
    let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);

    let pages = [];

    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    return <div>

        {
            props.users.map(users => <div key={users.id} className={classes.user}>
                <div className={classes.visual}>
                    <div>
                        <img src={users.photos.small != null ? users.photos.small : userPhoto}
                             className={classes.userPhoto}/>
                    </div>
                    <div>
                        {users.followed
                            ? <button onClick={() => {
                                props.unFollow(users.id)
                            }}>Unfollow</button>
                            : <button onClick={() => {
                                props.follow(users.id)
                            }}>Follow</button>}
                    </div>
                </div>
                <div className={classes.content}>
                    <div className={classes.name}>{users.name}</div>
                    <div>{users.status} <br></br></div>
                    <div>{"Russia"}</div>
                    <div>{"Moscow"}</div>
                </div>
            </div>)

        }
        <div>
            {pages.map(page => {
                return <span
                    className={props.currentPage === page && classes.selectedPage}
                    onClick={() => {
                        props.onPageChanged(page);
                    }}
                >{page} </span>
            })}
        </div>
    </div>

}

export default Users;


