const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT';
const TOOGLE_IS_LOADING = 'TOOGLE-IS-LOADING';


let initialState = {
    users: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage:1,
    isLoading: true
};
// reducer -Чистая функция, которая принимает State и action, скорее всего модифицирует State, по правилу immutable (благодаря callback'ам)
// И возвращает измененную копию State!!!!
const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        //callback func
        case FOLLOW:
            // так как нельзя изменять state  создаем его копию и возвращаем уже ее
            return {...state,
                    users: state.users.map( user => {
                        if (user.id === action.userId) {
                            return {...user, followed: true}
                        }
                        return user; })
            };
        //callback func
        case UNFOLLOW:
            // так как нельзя изменять state  создаем его копию и возвращаем уже ее
            return {...state,
                    users: state.users.map( user => {
                        if (user.id === action.userId){
                            return {...user, followed: false}
                        }
                        return user;})
            };
        //callback func
        case SET_USERS:
            return{...state,
                    users: [ ...action.users]};
        case SET_CURRENT_PAGE:
            return{...state,
                    currentPage: action.currentPage};
        case SET_TOTAL_USERS_COUNT: {
            return { ...state, totalUsersCount: action.count};
        }
        case TOOGLE_IS_LOADING: {
            return { ...state, isLoading: action.isLoading};
        }
        default: return state;
    }
}

// Создает Action , который нужен редьюсеру, для определения того какую callback func вызвать
// И передаёт параметр нужный callback'у
export const followActionCreator = (userId) => ({type: FOLLOW, userId})//action- type
export const unFollowActionCreator = (userId) => ({type: UNFOLLOW, userId})
export const setUserActionCreator= (users) => ({type: SET_USERS, users})
export const setCurrentPageActionCreator = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setUsersTotalCountActionCreator = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, count: totalUsersCount })
export const toggleIsLoadingActionCreator = (isLoading) => ({type: TOOGLE_IS_LOADING ,isLoading })

export default usersReducer;